.body {
  background-color: rgb(8, 8, 36);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: flex-start;
}

.noteContainer {
  display: flex;
  justify-content: center;
  z-index: 0;
}

.note {
  color:white;
  font-size: 23px;
  width: 10vw;
  height: 1px;
  text-align: center;
  animation: bounce linear alternate infinite;
}

.h1 {
  color: rgb(174, 174, 174);
  text-align: center;
  margin-top: 15vh;
  font-size: 20px;
  font-family: monospace;
  font-style: italic;
  margin-bottom: 2em;
}

.imgContainer {
  align-self: center;
  border-radius: 50%;
  margin-bottom: 2em;
  z-index: 1;
}

.img {
  border: 5px dotted green;
  border-radius: 50%;
  width: 20vw;
  height: auto;
  justify-content: center;
}

@keyframes bounce {
  from { transform: translate3d(0,10vh,0);}
  to   { transform: translate3d(0,90vh,0)}
}