.nav {
  position: absolute;
  border-bottom: 1px solid rgba(8, 3, 3, 0.741);
  box-shadow: 0px 2px 2px rgba(12, 22, 92, 0.407);
  padding: 1em 0em;
  width: 100%;
  height: 3vh;
  font-family: montserrat, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-style:italic;
}

.ul {
 display: flex;
 justify-content: end;
 align-content: center;
}

.li {
  margin-right: 1em;
  font-size: 18px;
  color: rgb(199, 199, 199);
  padding: 3px;
  border: transparent;
}

.li:hover {
  transition: border 500ms ease-out;
  border-bottom: 1px double white;
  cursor: pointer;
  margin-top: 1px;
}