.pianoContainer {
  display: flex;
  margin: 0 auto;
}

.whiteKey {
  height: 8em;
  width: 2.5em;
  background:linear-gradient(to bottom,#eee 0%,#fff 100%);
  border: 1px solid rgb(89, 81, 81);
  border-radius: 2.5px;
  box-shadow: -1px -1px 5px rgba(21, 21, 21, 0.704) inset;
}

.whiteKey:active {
  border-top:1px solid #777;
  border-left:1px solid #999;
  border-bottom:1px solid #999;
  box-shadow:2px 0 3px rgba(0,0,0,0.1) inset,-5px 5px 20px rgba(0,0,0,0.2) inset,0 0 3px rgba(0,0,0,0.2);
  background:linear-gradient(to bottom,#fff 0%,#e9e9e9 100%)
}

.whiteKey:hover {
  background: rgba(236, 221, 221, 0.799);
}

.blackKey {
  height: 4em;
  width: 1em;
  border:1px solid #000;
  border-radius:0 0 3px 3px;
  margin: 0 -.6em 0 -.6em;
  background: linear-gradient(45deg,rgb(13, 12, 12) 0%,rgb(18, 18, 18) 100%);
}

.blackKey:active {
  box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset,0 -2px 2px 3px rgba(0,0,0,0.6) inset,0 1px 2px rgba(0,0,0,0.5);
  background:linear-gradient(to right,rgb(25, 24, 24) 0%,#222 100%)
}

.blackKey:hover {
  background: rgba(0, 0, 0, 0.75);
}